.App {
    text-align: center;
}

.gradient-primary {
    background-color: #212529;

    background: -webkit-linear-gradient(50deg, #1a2980, #26d0ce);
    background: linear-gradient(40deg, #1a2980, #26d0ce);
}

.gradient-secondary {
    background-color: #212529;

    background: -webkit-linear-gradient(50deg, white, #dddddd);
    background: linear-gradient(40deg, white, #dddddd);
}

.gradient-text {
    -webkit-background-clip: text;
    background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

.drop-shadow {
    -webkit-filter: drop-shadow(0 5px 10px gray);
    filter: drop-shadow(0px 5px 10px gray);
}

.drop-shadow-dark {
    -webkit-filter: drop-shadow(0 5px 10px darkslategray);
    filter: drop-shadow(0px 5px 10px darkslategray);
}

section {
    position: relative;
}